
.vux-popup-dialog,.vux-popup{position:fixed;left:0;bottom:0;width:100%;background:#eee;z-index:501;transition-property:-webkit-transform;transition-property:transform;transition-property:transform, -webkit-transform;transition-duration:300ms
}
.vux-popup-mask{display:block;position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.5);opacity:0;tap-highlight-color:rgba(0,0,0,0);z-index:-1;transition:opacity 400ms
}
.vux-popup-mask.vux-popup-show{opacity:1
}
.vux-popup-animate-enter{-webkit-transform:translate3d(0, 100%, 0);transform:translate3d(0, 100%, 0)
}
.vux-popup-animate-leave-active{-webkit-transform:translate3d(0, 100%, 0);transform:translate3d(0, 100%, 0)
}


