
.vux-swipeout{width:100%;overflow:hidden
}
.vux-swipeout-item{position:relative
}
.vux-swipeout-button-box{position:absolute;top:0;right:0;bottom:0;left:0;font-size:0;text-align:right
}
.vux-swipeout-button-box-left{text-align:left
}
.vux-swipeout-button-box>div{height:100%
}
.vux-swipeout-button{height:100%;text-align:center;font-size:14px;color:#FFF;border:none
}
.vux-swipeout-content{position:relative;background:#FFF
}
.vux-swipeout-content.vux-swipeout-content-animated{transition:-webkit-transform .2s;transition:transform .2s;transition:transform .2s, -webkit-transform .2s
}
.vux-swipeout-button-primary{background-color:#1AAD19
}
.vux-swipeout-button-warn{background-color:#E64340
}
.vux-swipeout-button-default{background-color:#c8c7cd
}


