
.vux-bg-blur{z-index:-2;opacity:0;position:absolute;min-height:100%;display:block;top:0;max-height:none;left:-20%;top:-20%;width:140%;height:140%;transition:opacity linear .8s
}
.vux-bg-blur-overlay{z-index:-1;position:absolute;width:100%;height:100%;background:linear-gradient(to bottom, rgba(0,0,0,0.15) 0, #000 100%)
}


